@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
#root {
  height: 100%;
}
.App {
  background: linear-gradient(45deg, #7f8994, #111319);
  background-attachment: fixed;
  padding: 0 2rem;
  min-height: 100%;
}

@media (max-width: 370px) {
  .App {
    padding: 0;
  }
}

footer {
  color: white;
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 0.8rem;
  padding: 3rem 0 1rem;
}

footer p {
  margin: 0;
}

footer a {
  color: white;
  font-weight: 700;
  text-decoration: none;
}

.Filters{display:flex;flex-direction:row;color:#fff;font-family:"Noto Sans JP",sans-serif;font-weight:700;padding:1rem}.Filters .filters-container{display:flex;flex-wrap:wrap}.Filters .filters-container>div{margin:.5rem 0}.Filters h1,.Filters h2{text-transform:uppercase;font-size:1rem}.Filters h2{margin:0 .5rem 0 2rem;font-weight:500;color:#768499;min-width:90px}.Filters .option{height:30px;width:30px;cursor:pointer;margin:0 .1rem}.Filters .attr-filter,.Filters .compl-filter,.Filters .role-filter{display:flex}.Filters .attr-filter img.attr{-webkit-filter:grayscale(1);filter:grayscale(1)}.Filters .attr-filter img.attr.str{-webkit-filter:grayscale(1) brightness(1.8);filter:grayscale(1) brightness(1.8)}.Filters .attr-filter img.attr.active{-webkit-filter:grayscale(0);filter:grayscale(0)}.Filters .compl-filter .compl{background:#acacac;position:relative}.Filters .compl-filter .compl.active{background:#dedede}.Filters .compl-filter .compl::before{content:"";position:absolute;height:14px;width:14px;left:8px;top:8px;transform:rotate(45deg);background:#111319}.Filters .role-filter .role-options{display:flex;flex-wrap:wrap}.Filters .role-filter .role-options .text-option{padding:0 .5rem;color:#acacac;cursor:pointer}.Filters .role-filter .role-options .text-option.active{color:#fff}@media(max-width: 340px){.Filters{flex-direction:column}}
.HeroesList{display:flex;flex-wrap:wrap;justify-content:space-evenly;margin:0;padding:0}.loading{color:#fff;font-family:"Noto Sans JP",sans-serif;font-weight:700;font-size:3rem;text-align:center;padding:2rem 0}
.Hero{list-style:none;position:relative;margin:1rem;overflow:hidden;height:144px;box-shadow:2px 2px 5px #222;transition:height .5s;cursor:default}.Hero img.avatar{height:144px;width:256px}.Hero .hero-label,.Hero .hero-details{position:absolute;box-sizing:border-box;display:flex;padding:0 1rem;width:256px;color:#fff;font-family:"Noto Sans JP",sans-serif;font-weight:700}.Hero .hero-label{align-items:center;background:linear-gradient(rgba(0, 0, 0, 0), black);height:60px;bottom:0px}.Hero .hero-label img.attr{height:30px;width:30px;position:relative;top:3px}.Hero .hero-label h2{margin:0 0 0 10px;line-height:1}.Hero .hero-details{top:-84px;background:linear-gradient(black 0%, rgba(0, 0, 0, 0.8) 60%, rgba(0, 0, 0, 0) 100%);height:84px;transition:top .5s;padding:.5rem;flex-direction:column}.Hero .hero-details .hero-complexity{display:flex;flex-direction:row;width:256px;justify-content:space-evenly}.Hero .hero-details .hero-complexity .complexity-indicator{position:relative;width:10px;height:10px;transform:rotate(45deg);border:2px solid #fff;top:5px}.Hero .hero-details .hero-complexity .complexity-indicator.filled{background:#fff}.Hero .hero-details .roles-list{display:flex;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}.Hero .hero-details .roles-list .roles-item{padding:0 .3rem}.Hero:hover .hero-details{top:0px}
